.main_header {
  background-image: url(../images/background.jpg);
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
  
}


.main_header-container {
  height: 100%;
  display: grid; 
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: space-between;
  gap: 6rem;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.75));
}

.main_header-left {
  margin-top: -4rem;
  margin-left: 2.5rem;
}
.main_header-left p {
  margin-bottom: 1rem;
  font-weight: lighter;
}
.introducing {
  margin-bottom: 1rem;
  margin-top: 5rem;
}
.fridgesmart {
  margin-bottom: 1rem;
}

.main_header-right {
  display: grid;
  place-items: center;
  position: relative;
}

.plate {
  height: 3rem;
  z-index: 10;
  position: relative;
  left: 100px;
  bottom: 60px;

}
.robot {
  min-width: 16.36rem;
  height: 17.44rem;
  z-index: 8;
  margin-top: 6rem;
}
.main_header-plate {
  position: relative;
  
}
.main_header-image {
  position: absolute;
  
}

.main_header-right img {
  animation: updown 3s linear infinite;
}
@keyframes updown {
    0% {
        transform: translateY(-20px);
    }
    50% {
        transform: translateY(20px);
    }
    100% {
        transform: translateY(-20px);
    }
}
